export default {
  name: "ExpressDelivery",
  data() {
    return {
      express: {
        name: "",
        phone: "",
        no: "",
        address:"",
        itemtype:[],
        weight:[],
        cost:0
      },
      thingType: [["文件", "数码产品", "生活用品", "服饰", "食品", "其他"]],
      weight:[["小件(重量低于5kg)", "大件(重量大于等于5kg)"]],
      user: {
        isverify: true
      }
    }
  },
  created(){
    this.getUserInfo();
  },
  methods: {
    getUserInfo() {
      this.axios.post("/user/get", {}).then(response => {
        var user = response.data.data;
        this.user = user;
      });
    },
    verify() {
      this.$router.push('/verify');
    }
  }
}



